<script>
    import headerDefault from './header-default'

    export default {
        name: "PRINT",
        props: {
            set: Object,
            cs: Object,
            img: String,
            rt: String,
        },
        data() {
            return {}
        },
        components: {headerDefault},
        created() {
        },
        methods: {},
    }
</script>

<style lang="scss">
    .print-wrapper-fixed {
        width: 100%;
        margin: 0 auto;
        min-height: 100vh;
        background-color: #fff;
        color: #000;

        .print-wrapper-body {
